<template>
  <div class="backshadow">
    <div class="modal">
        <slot></slot>
        <a class="btn" @click="emitClose">- X -</a>
    </div>
  </div>
</template>

<script>
export default {
    methods: {
        emitClose(){
            this.$emit('close')
        }
    }
}
</script>

<style>
    .backshadow{
        position: fixed;
        top: 0;
        background-color: rgba(0, 0, 0, 0.7);
        width: 100vw;
        height: 100vh;
        z-index: 201;
        overflow: hidden;
    }
    .modal{
        position: relative;
        max-width: 400px;
        margin: 100px auto 0 auto;
        background: radial-gradient(#3c717a6c 10%, transparent 70%);
        color: white;
        border-radius: 10px;
        text-align: center;
    }

    .modal img{
        width: 200px;
    }

    .modal a.btn{
        position: absolute;
        right: 0;
        top: -50px;
        background-color: #3c717a6c;
        user-select: none;
    }
</style>