<template>
    <form id="contact-form">
        <label>İsim:</label>
        <input type="text" v-model="contact_data.name" placeholder="Örnek: Yunus">

        <label>Telefon Numarası:</label>
        <input type="tel" v-model="contact_data.phone" placeholder="Örnek: +90 012 345 67 89">

        <label>Gmailiniz:</label>
        <input type="email" v-model="contact_data.gmail" placeholder="Örnek: benimgmailim@gmail.com">

        <label>Birde mesaj yazın tam olsun :)</label>
        <textarea id="" rows="9" v-model="contact_data.description"></textarea>
        
        <a href="#">Gönder</a>
    </form>
</template>

<script>
export default {
    data(){
        return{
            contact_data: {name: '', phone: '', gmail: '', description: ''}
        }
    }
}
</script>

<style>
    #contact-form{
        padding: 5px;
        text-align: left;
    }

    #contact-form textarea{
        background-color: rgb(12, 26, 37);
        border-radius: 10px;
        resize: none;
        border: none;
        font-size: 1.1em;
    }

    #contact-form input, textarea{
        box-sizing: border-box;
        display: block;
        width: 100%;
        padding: 10px;
        margin: 20px 0;
        text-align: left;
        background-color: transparent;
        color: white;
        border: none;
        border-bottom: 1px solid white;
        outline: none;
    }

    #contact-form input::placeholder{
        color: rgb(191, 221, 223);
        opacity: 0.2;
    }

    #contact-form a{
        display: block;
        margin: 0 auto;
        max-width: 200px;
        text-align: center;
    }
    
</style>