<template>
    <div class="card" :class="{hovercard: isHoverable}" :style="[{'--expanded-card-size': this.expandsize}, {'--card-width': this.width}, {'--card-height': this.height} ]">
        <span v-if="isHoverable" class="reveal">Göstermek için tıkla</span>
        <slot name="cardtop"></slot>
        <slot name="cardexpanded"></slot>
    </div>
</template>

<script>
export default {
    props: ['gamecarddata', 'isHoverable', 'expandsize', 'height', 'width']
}
</script>

<style>
    .card{
        position: relative;
        margin: 10px;
        height: var(--card-height);
        background-color: rgba(66, 76, 160, 0.425);
        border-radius: 10px;
        overflow: hidden;
        transition: height 100ms;
        flex: 0 0 var(--card-width);
        text-align: center;
        max-width: 600px;
    }

    .card .gameimage{
        height: var(--card-height);
        width: 100%;
        object-fit: cover;
        padding: 10px;
        box-sizing: border-box;
    }

    .card.hovercard:hover{
        /* If the variable not exists then set to 600px */
        height: 600px;
        height: var(--expanded-card-size);
    }

    .card .showgame{
        padding: 0 20px;
    }

    .card a{
        display: inline-block;
        background-color: white;
        color: black;
    }

    .card a:hover{
        background-color: rgb(94, 108, 124);
        color: white;
    }

    .card .reveal{
        display: block;
        position: absolute;
        width: 100%;
        user-select: none;
    }

    @media screen and (max-width: 768px) {
        .card h1{
            font-size: large;
        }
    }


</style>