      understandableword: ''
    }
  },
  components: { Gamecard, Contactform, Modal },
  methods: {
    handleScroll() {
      let scrollAmount = window.scrollY
      if(scrollAmount < window.innerHeight){
        this.$refs.spacebg.style.top = (scrollAmount * 0.5) + 'px'
        this.$refs.clouds.style.top = (scrollAmount * 0.2) + 'px'
        this.$refs.titletext.style.top = (scrollAmount * 0.5) + 'px'
        this.navbar.style.top = (scrollAmount * 0.85) + 'px'
      }
    },
    getWindowWidth(){
      return window.innerWidth
    },
    toggleTeammateModal(teammatemodal){
      teammatemodal = !teammatemodal
      if(teammatemodal){
        document.body.setAttribute('style', 'overflow: hidden;')
      }
      else{
        document.body.removeAttribute('style')
      }
      return teammatemodal
    }
  },
  beforeMount(){
    window.addEventListener('scroll', this.handleScroll)
    this.navbar = document.getElementById('navbar')
    this.understandableword = this.understandablewordslol[Math.floor(Math.random() * this.understandablewordslol.length)]
    this.teammate_count = this.team.length
  },
  beforeUnmount(){
    window.removeEventListener('scroll', this.handleScroll)
  }
}
</script>

