<template>
  <!-- We gave id because were using in homeview(mounted()) -->
  <nav id="navbar" :class="{'navbar-mobile': showMenuButton}">
    <img id="logo" alt="Vue logo" src="./assets/logo.png">
    
    <!-- For desktop -->
    <div v-show="!showMenuButton">
      <router-link to="/">Ana Sayfa</router-link>
      <router-link to="/about">Hakkımızda</router-link>
    </div>

    <!-- For mobile -->
    
    <div v-show="showMenuButton">
      <a id="menubutton" v-show="showMenuButton">
        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 448 512"><!--! Font Awesome Pro 6.1.1 by @fontawesome - https://fontawesome.com License - https://fontawesome.com/license (Commercial License) Copyright 2022 Fonticons, Inc. -->
          <path d="M0 96C0 78.33 14.33 64 32 64H416C433.7 64 448 78.33 448 96C448 113.7 433.7 128 416 128H32C14.33 128 0 113.7 0 96zM0 256C0 238.3 14.33 224 32 224H416C433.7 224 448 238.3 448 256C448 273.7 433.7 288 416 288H32C14.33 288 0 273.7 0 256zM416 448H32C14.33 448 0 433.7 0 416C0 398.3 14.33 384 32 384H416C433.7 384 448 398.3 448 416C448 433.7 433.7 448 416 448z"/>
        </svg>
      </a>
      <div id="mobilemenu">
        <div class="links">
          <router-link to="/">Ana Sayfa</router-link>
          <router-link to="/about">Hakkımızda</router-link>
        </div>
        
      </div>
    </div>
  </nav>
  <router-view/>
  <Footer :aboutmsg="aboutmsg" :contact="contactmsg" >
    <template v-slot:whatwedo>
      <ul>
        <li v-for="service in services" :key="service.id">
          <!-- ROUTER EKLENECEK -->
          <a :href="service.link">{{service.name}}</a>
        </li>
      </ul>
    </template>
  </Footer>
</template>

<script>
import Footer from './components/Footer.vue'
  
export default {
  data(){
    return{
      aboutmsg: 'Lorem ipsum dolor sit amet consectetur adipisicing elit. Aut, dolor! Reprehenderit eum, possimus sit repellat atque voluptatem sapiente distinctio ducimus explicabo, veritatis dolorum molestiae mollitia. In officia autem sit porro omnis facere aperiam dolorum deserunt nostrum, alias ut nihil libero temporibus doloribus neque aut, expedita dicta iste quis voluptatum laborum?',
      contactmsg: {address: 'lol idk', phone: 'lol idk', location: 'wow bro were going too fast man'},
      services: [
        {name: 'NOTHING LOL', link: '#'},
        {name: 'AAAAAAA', link: '#'}
      ],
      showMenuButton: false,
      timeouts: {Resize: null}
    }
  },
  components: { Footer },
  mounted(){
    window.addEventListener('resize', this.changeAspect)
    if(window.innerWidth <= 768){
      this.showMenuButton =  true
    }
    else if(this.showMenuButton){
      this.showMenuButton =  false
    }
  },
  methods: {
    changeAspect(){
      clearTimeout(this.timeouts.Resize)
      this.timeouts.Resize = setTimeout(() => {
        if(window.innerWidth <= 768){
          this.showMenuButton =  true
        }
        else if(this.showMenuButton){
          this.showMenuButton =  false
        }
      }, 200);
      
    }
  }
}
</script>

